import icons from '../../assets/models/*.svg';

import { MODELS_MAP } from './constants';

/**
 *     LLM,  
 * @param {String} modelId  LLM
 * @returns {String}   
 */
export const getModelIcon = (modelId) => {
     const modelName = MODELS_MAP[modelId];
    const src = icons[modelName];

    return src;
};
